.App 
{ 
  position: absolute;
  width:100%;
  height:100%;
  text-align: center;
  margin:0px;
  font-family: 'Microsoft YaHei UI'; 
  display: flex;
  align-items: center;
  justify-content: center;
 
}

.AppDarkMask
{
  background-color: #2c2c2c;  
  position: absolute;
  width:100%;
  height:100%;
  opacity: 0.9;
}


.CT_PanelRoot
{
  /* background-color: purple; */
  position: relative;
  width:100%;
  height:90%;
  min-height: 400px; 
  overflow: visible;
  margin: 0px;
}

.CT_PanelContent
{ 
  position: absolute;
  width:100%;
  height:100%;  
  overflow: hidden;
 
}

.LoginAndRegisterWrapper
{ 
  position: absolute;
  left:0%;
  width:200%;
  height: 100%;
  transition: left  0.5s ease-out;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
 
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

